import Vue from 'vue'
import Router from 'vue-router'

const StartPage = () => import('@/view/StartPage/StartPage')//banner

const Register = () => import('@/view/register/register') //
const registerInfo = () => import('@/view/register/registerInfo') //
const taobao = () => import('@/view/register/taobao') //
const success = () => import('@/view/register/success') //
const Login = () => import('@/view/login/login') //
const Index = () => import('@/view/Index/Index') //

const Home = () => import('@/view/Home/Home')//
//
const TaskList = () => import('@/view/TaskList/TaskList') //
const NewTask = () => import('@/view/TaskList/NewTask') //

const HistoricalTask = () => import('@/view/HistoricalTask/HistoricalTask') //
//
const AccountInfo = () => import('@/view/AccountInfo/AccountInfo') //

Vue.use(Router)

export default new Router({
  // mode:"history",
  base:__dirname,
  routes: [
    {
      //banner
      path: '/startPage',
      name: 'banner',
      component: StartPage,
    },
    {
      //
      path: '/login',
      name: 'login',
      component: Login,
    },
    {
      //
      path: '/Register/registerInfo',
      name: 'Register',
      component: Register,
      children: [
        {
          //
          path: '/Register/registerInfo',
          name: 'registerInfo',
          component: registerInfo
        },
        {
          //
          path: '/Register/taobao',
          name: 'taobao',
          component: taobao
        },
        {
          //
          path: '/Register/success',
          name: 'success',
          component: success
        }
      ]
    },
    {
      //
      path: '/Index',
      name: 'Index',
      component: Index,
      children: [
        //
        {
          path: '/taskList',
          name: '',
          component: TaskList
        },
        {
          path: '/newTask',
          name: '',
          component: NewTask
        },
        {
          path: '/historicalTask',
          name: '',
          component: HistoricalTask
        },
        //
        {
          path: '/accountInfo',
          name: '',
          component: AccountInfo
        },
        //
        {
          path: '/home',
          name: '',
          component: Home
        },
        

      ]
    },
    {
      path: '/',
      name:"startPage",
      redirect: '/startPage',
      component: StartPage
    }
  ]
})



